@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.slider_container{
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  position: relative;
  background-color: #000;
}

.slider{
  width: 300vw;
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 0;
  transition: left .6s;
  background-color: #000;
}


.page1_container {
  width: 100vw;
  height: 100vh;
  transition: background 2s;
  position: relative;
  overflow: hidden;
}

.night{
  background:url(../../static/media/night.41bcd898.jpg) center center/cover no-repeat;
}

.day{
  background:url(../../static/media/day.17045182.jpg) center center/cover no-repeat;
}

.mobile_navigation {
  display: none;
}

.hill {
  position: absolute;
  bottom: 0;
  left: 0px;
  width: 100%;
  z-index: 100;
  transition: -webkit-filter 1s;
  transition: filter 1s;
  transition: filter 1s, -webkit-filter 1s;
}

.color_white {
  color: #f9f9f9;
}

.hero {
  display: flex;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -80%);
  font-family: 'Sansita Swashed', cursive;
  font-size: 150px;
  z-index: 50;
  bottom: 30%;
}

.st {
  -webkit-animation: appear 1s forwards;
          animation: appear 1s forwards;
}

.nd {
  opacity: 0;
  transform: translateY(200%);
  -webkit-animation: appear 0.7s forwards 0.5s;
          animation: appear 0.7s forwards 0.5s;
}

.moon {
  position: absolute;
  top: 10%;
  left: 50%;
  z-index: 110;
  object-fit: contain;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  -webkit-animation: moonGlow 10s infinite;
          animation: moonGlow 10s infinite;
  cursor: pointer;
  background: url(../../static/media/moon.01b97fca.png)
    center center/cover no-repeat;
  transform: translateX(-50%) translateY(-50vh);
  transition: transform 2s,opacity 2s,-webkit-filter 2s;
  transition: transform 2s,filter 2s,opacity 2s;
  transition: transform 2s,filter 2s,opacity 2s,-webkit-filter 2s;
}

.sun {
  position: absolute;
  top: 10%;
  left: 50%;
  z-index: 110;
  width: 400px;
  height: 400px;
  -webkit-animation: sunGlow 10s infinite;
          animation: sunGlow 10s infinite;
  cursor: pointer;
  background: url(../../static/media/sun.16147ee8.png)
    center center/cover no-repeat;
  transform: translateX(-50%) translateY(-50vh);
  transition: transform 2s,opacity 2s,-webkit-filter 2s;
  transition: transform 2s,filter 2s,opacity 2s;
  transition: transform 2s,filter 2s,opacity 2s,-webkit-filter 2s;
}

@-webkit-keyframes fall {
  from {
    transform: translateX(-50%) translateY(-100%);
  }
  to {
    transform: translateX(-50%) translateY(0%);
  }
}

@keyframes fall {
  from {
    transform: translateX(-50%) translateY(-100%);
  }
  to {
    transform: translateX(-50%) translateY(0%);
  }
}

@-webkit-keyframes moonGlow {
  0% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }

  50% {
    -webkit-filter: brightness(150%);
            filter: brightness(150%);
  }

  100% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
}

@keyframes moonGlow {
  0% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }

  50% {
    -webkit-filter: brightness(150%);
            filter: brightness(150%);
  }

  100% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
}

@-webkit-keyframes sunGlow {
  0% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }

  50% {
    -webkit-filter: brightness(150%);
            filter: brightness(150%);
  }

  100% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
}

@keyframes sunGlow {
  0% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }

  50% {
    -webkit-filter: brightness(150%);
            filter: brightness(150%);
  }

  100% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
}

@-webkit-keyframes appear {
  from {
    opacity: 0;
    transform: translateY(200%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes appear {
  from {
    opacity: 0;
    transform: translateY(200%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 1150px) {
  .hero {
    display: block;
    font-size: 100px;
    bottom: 20%;
    text-align: center;
  }

  .hill {
    position: absolute;
    bottom: 0;
    left: 0px;
    width: 100%;
    height: 300px;
    z-index: 100;
    object-fit: cover;
  }

  .sun {
    width: 180px;
    height: 180px;
  }

  .moon {
    width: 130px;
    height: 130px;
  }

  .btn {
    display: none !important;
  }

  .mobile_navigation {
    display: flex;
    position: absolute;
    bottom: 27%;
    left: 50%;
    z-index: 150;
    transform: translateX(-50%);
  }

  .about_me_mobile,
  .my_work_mobile {
    border: none;
    background-color: #fff;
    padding: 5px;
    border-radius: 10px;
    align-items: center;
    justify-content: center;
    width: 130px;
    font-size: 23px;
    font-family: 'Roboto', sans-serif;
    outline: none;
  }
  .about_me_mobile {
    margin-right: 10px;
    display: flex;
    flex-direction: row;
  }
  .my_work_mobile {
    margin-left: 10px;
    display: flex;
    flex-direction: row;
  }
}

@media only screen and (max-height: 750px) {
  .moon,
  .sun {
    width: 180px;
    height: 180px;
    top: 5%;
  }

  .moon {
    width: 130px;
    height: 130px;
  }

  .hero {
    bottom: 10%;
  }

  .mobile_navigation{
    bottom: 10%;
  }
}


@media (min-width: 1200px) {
  .hero {
    font-size: 100px;
    bottom: 35%;
  }
  .sun {
    width: 240px;
    height: 240px;
  }
}

@media (min-width: 1600px) {
  .hero {
    font-size: 150px;
    bottom: 30%;
  }
  .sun {
    width: 400px;
    height: 400px;
  }
}

.btn {
  border: none;
  align-self: center;
  display: block;
  font-size: 20px;
  background-color: #fff;
  width: 150px;
  padding: 10px;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.about_me_btn {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  -webkit-animation: leftToRight 1s forwards;
          animation: leftToRight 1s forwards;
}
.my_work_btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-animation: rightToLeft 1s forwards;
          animation: rightToLeft 1s forwards;
}

.btn:hover {
  background-color: #333;
  color: #fff;
}

@-webkit-keyframes leftToRight {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(-50px);
  }
}

@keyframes leftToRight {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(-50px);
  }
}

@-webkit-keyframes rightToLeft {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(50px);
  }
}

@keyframes rightToLeft {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(50px);
  }
}

@media (max-width: 600px){
  .sun {
    width: 300px;
    height: 300px;
  }
}
.about_container {
  background-color: black;
  width: 100vw;
  height: 100vh;
}

.work_container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  position: relative;
  background-color: #000;
}

.mobile_back {
  display: none;
}

.projects_slider {
  width: 80vw;
  height: 700px;
  overflow: hidden;
}
.projects_container {
  position: relative;
}

.projects_slide {
  display: flex;
  transition: transform 1s;
  justify-content: space-between;
}

.arrow {
  color: #fff;
  background: none;
  border: none;
  position: absolute;
  top: 50%;
  transform: translateY(-80%);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  outline: none;
  z-index: 200;
}

.arrow_left {
  left: -60px;
}

.arrow_right {
  right: -70px;
}

.fs_large {
  font-size: 70px !important;
}

.back {
  position: absolute;
  top: 5%;
  left: 0.5%;
  padding: 5px;
  font-size: 16px;
  background-color: rgb(223, 139, 139);
  color: #fff;
}

.back:hover {
  background-color: #fff;
  color: #000;
}

@media (min-width: 1300px) and (max-width: 1400px) {
  .projects_slider {
    width: 80vw;
    height: 600px;
    overflow: hidden;
    font-size: 14px;
  }

  .back {
    position: absolute;
    top: 5%;
    left: 0;
    margin: 0;
    padding: 0;
    font-size: 14px;
    background-color: rgb(223, 139, 139);
    color: #fff;
    width: auto;
    padding: 15px;
    z-index: 500;
  }
}

@media (min-width: 800px) and (max-width: 1300px) {
  .back {
    position: absolute;
    top: 5%;
    left: 0;
    margin: 0;
    padding: 0;
    font-size: 10px;
    background-color: rgb(223, 139, 139);
    color: #fff;
    width: auto;
    padding: 7px;
    display: flex !important;
    margin-right: 10px;
    z-index: 200;
  }
}

@media (max-width: 800px) {
  .back {
    display: none;
  }
  .mobile_back {
    display: block;
    position: absolute;
    top: 85%;
    right: 3%;
    outline: none;
    font-size: 40px;
    border: none;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    z-index: 200;
    background-color: rgb(223, 160, 160);
  }

  .arrow_left {
    left: -30px;
  }

  .arrow_right {
    right: -40px;
  }

  .fs_large {
    font-size: 70px !important;
  }
}

.project {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80vw !important;
  height: 100%;
}

.btn_mob {
  display: none;
}

.previews {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 80vw;
}

.preview_img {
  width: 750px;
  height: 400px;
  border-radius: 10px;
}

.preview_img_mobile {
  height: 400px;
  border-radius: 10px;
}

.info {
  margin-top: 40px;
  background-color: rgba(51, 51, 51, 0.336);
  width: 90%;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  border-radius: 7px;
}

h3 {
  margin-bottom: 10px;
  font-size: 25px;
  font-family: 'Noto Sans', sans-serif;
  text-align: center;
}

p {
  margin-bottom: 20px;
  text-align: center;
  font-family: 'Noto Sans', sans-serif;
}

.buttons {
  display: flex;
  width: 40%;
  justify-content: space-between;
}

.buttons a {
  text-decoration: none;
  color: #000;
  font-family: 'Noto Sans', sans-serif;
  text-align: center;
  display: flex;
  align-items: center;
  width: auto;
}

@media (min-width: 1300px) and (max-width: 1400px) {
  .btn {
    font-size: 14px;
    padding: 5px;
  }
}

@media (max-width: 1300px) {
  .preview_img_mobile {
    display: none;
  }
}

@media (max-width: 800px) {
  .preview_img {
    width: 300px;
    height: auto;
    border-radius: 10px;
  }

  .btn_mob {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 10px;
    color: #000;
    text-decoration: none;
    font-size: 18px;
    font-family: 'Noto Sans', sans-serif;
  }
}

